.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.footer{
  width: 100%;
  clear: both;
  text-align: center;
  padding-bottom: 10px;
}
h3 {
  text-align: center;
}
